import React, { useState, useContext, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import { FiUser, FiLock, FiAlertCircle, FiInfo } from 'react-icons/fi';

const AdminLogin = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loginError, setLoginError] = useState('');
  const { login, isAuthenticated, loading, error } = useContext(AuthContext);
  const navigate = useNavigate();
  const location = useLocation();
  
  const from = location.state?.from?.pathname || '/admin';
  
  useEffect(() => {
    // If already authenticated, redirect to admin dashboard
    if (isAuthenticated) {
      navigate(from, { replace: true });
    }
  }, [isAuthenticated, navigate, from]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoginError('');
    
    if (!username.trim() || !password.trim()) {
      setLoginError('Username and password are required');
      return;
    }
    
    const success = await login(username, password);
    if (success) {
      navigate(from, { replace: true });
    } else {
      setLoginError(error || 'Login failed. Please check your credentials.');
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100 dark:bg-gray-900">
      <div className="w-full max-w-md p-8 bg-white dark:bg-gray-800 rounded-lg shadow-md">
        <div className="text-center mb-6">
          <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-red-600 text-white mb-4">
            <FiUser size={32} />
          </div>
          <h1 className="text-2xl font-bold text-gray-800 dark:text-white">Admin Login</h1>
        </div>
        
        {/* Demo credentials info */}
        <div className="p-3 mb-6 text-sm bg-blue-50 dark:bg-blue-900/30 text-blue-800 dark:text-blue-300 rounded-lg flex items-start">
          <FiInfo className="mt-0.5 mr-2 flex-shrink-0" />
          <div>
            <p className="font-medium">Demo Credentials</p>
            <p className="mt-1">Username: <span className="font-mono bg-blue-100 dark:bg-blue-900 px-1 py-0.5 rounded">admin</span></p>
            <p className="mt-0.5">Password: <span className="font-mono bg-blue-100 dark:bg-blue-900 px-1 py-0.5 rounded">admin123</span></p>
          </div>
        </div>
        
        {loginError && (
          <div className="p-3 mb-4 text-sm text-red-700 dark:text-red-300 bg-red-100 dark:bg-red-900/30 rounded-lg flex items-center" role="alert">
            <FiAlertCircle className="mr-2 flex-shrink-0" />
            <span>{loginError}</span>
          </div>
        )}
        
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label className="block mb-2 text-sm font-medium text-gray-700 dark:text-gray-300" htmlFor="username">
              Username
            </label>
            <div className="relative">
              <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none text-gray-500">
                <FiUser />
              </div>
              <input
                id="username"
                type="text"
                className="form-input pl-10"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                placeholder="Enter your username"
                required
              />
            </div>
          </div>
          
          <div className="mb-6">
            <label className="block mb-2 text-sm font-medium text-gray-700 dark:text-gray-300" htmlFor="password">
              Password
            </label>
            <div className="relative">
              <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none text-gray-500">
                <FiLock />
              </div>
              <input
                id="password"
                type="password"
                className="form-input pl-10"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Enter your password"
                required
              />
            </div>
          </div>
          
          <button
            type="submit"
            className="w-full py-2 px-4 bg-red-600 hover:bg-red-700 text-white font-medium rounded-lg transition duration-200 flex items-center justify-center"
            disabled={loading}
          >
            {loading ? (
              <>
                <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                <span>Logging in...</span>
              </>
            ) : 'Login'}
          </button>
        </form>
      </div>
    </div>
  );
};

export default AdminLogin; 